// reset
@charset "utf-8";

// Noto-Sans

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;400;500;700;900&display=swap');

*{
    box-sizing: border-box;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 0;
    line-height: 1.2;
    text-transform: uppercase;
}

*::before, *::after{
	border-spacing: 0;
}

::selection{
    background: none;
}

html, body, header, main, footer, 
h1, h2, h3, h4, h5, h6, figure, 
div, span, p, a, input, dl, dt, dd{
    margin: 0; padding: 0;
}
img, video, iframe{
    width: 100%; height: 100%;
    margin: 0; padding: 0;
    vertical-align: middle;
    object-fit: cover;
}
a{text-decoration: none; color: inherit;}
ol,ul,li{
    margin: 0; padding: 0;
    list-style-type: none;
}
table {
    border-collapse: collapse;
	border-spacing: 0;
}
input, button{
    border: none;
    background: none;
    &:focus{
        outline: none;
    }
}
select, select:focus, option{
    border: none;
    background-color: #fff;
}

// color
$color:rgb(12,76,160);
$color2:rgb(151, 11, 18);

// width

.mwidth{
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 10px
}

// ================================================================================
// ============================= header ===========================================
// ================================================================================
header{
    // background-color: $color2;
    .head{
        height: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .h-left a{
            display: flex;
            align-items: center;
            img{
                width: 28px;
                margin-right: 4px;
            }
            h1{
                color: #000;
                font-size: 18px;
                letter-spacing: 2px;
            }
        }
        .h-right{
            display: flex;
            a img{
                width: 24px;
                margin-right: 20px;
            }
            .bugermenu img{
                height: 32px;
                cursor: pointer;
            }
        }
    }
    .menu{
        width: 100%; height: 100%;
        background-color: rgba(0,0,0,0.8);
        position: fixed;
        left: 100%; top: 0;
        z-index: 100;
        .menuclose{
            width: 24px; height: 24px;
            position: relative;
            transform: translateX(68vw);
            cursor: pointer;
            span{
                width: 100%;
                border-bottom: 2px solid #000 ;
                position: absolute;
                left: 0; top: 50%;
                &:nth-of-type(1){
                    transform: rotate(45deg);
                }
                &:nth-of-type(2){
                    transform: rotate(-45deg);
                }
            }
        }
        .menu-contents{
            width: 100%; height: 100%;
            padding: 18px 40px 10px;
            background-color: #fff;
            border-radius: 40px;
            transform: translateX(60%);
            transition: .5s;
            .menu-title{
                margin: 20px 10px;
                font-size: 18px;
                font-weight: 700;
                color: $color;
            }
            ul{
                width: 100%;
                margin-bottom: 20px;
                cursor: pointer;
            }
            li{
                width: 100%;
            }
            li:hover{
                background-color: $color;
                color:#fff;
            }
            a{  
                display: inline-block;
                width: 100%;
                margin: 4px 20px;
                font-size: 1.2em;
            }
        }
    }
    .menu.active{
        left:0;
        .menu-contents{
            transform: translateX(10%);
        }
    }
}

// ================================================================================
// ============================= footer ===========================================
// ================================================================================

.footer{
    text-align: center;
    margin: 4px 0;
    span{
        font-size: .8em;
        font-weight: 200;
    }
    span:nth-of-type(1){
        text-transform: lowercase;
        margin-right: 10px;
    }
}